<template>
  <div class="All">
  
    <div v-for="stream in streams" v-bind:key="stream.alias">
      <div class="video-container">
        <hls-video :video-src="streamUri + '/' + stream.streamUri" />
        <div class="stream-label">
          {{ stream.alias }}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      getListURI: "http://" + domain + ":" + httpPort + "/list",
      stopURI: "http://" + domain + ":" + httpPort + "/stop",
      startURI: "http://" + domain + ":" + httpPort + "/start",
      streamUri: "http://" + domain + ":" + httpPort,
      streams: null,
    };
  },
  methods: {
    async getStreams() {
      console.log(this.getListURI);
      const response = await fetch(this.getListURI);
      return response.json();
    },
  },
  async mounted() {
    this.streams = await this.getStreams();
  },
};
</script>

